   sass root. Don't edit the version
   in node_modules.
----------------------------------------
Custom project SASS goes here.

i.e.
@include u-padding-right('05');
----------------------------------------
*/

@use "uswds-core" as *;

:root {
   --hmda-blue: #1f498f;
   --hmda-blue-opacity: rgba(31, 73, 143, .8);
   --hmda-blue-light: #2360C2;
   --hmda-blue-bright: #3B7BE9;
   --hmda-blue-lightest: #00C8FF;
   --hmda-blue-dark: #163851;
   --hmda-blue-dark-opacity: rgba(22, 46, 81, .9);
   --usa-blue: #0071bc;
   --cfpb-green: #20aa3f;
}

body {
   margin-bottom: 0 !important;
}

/*************************
   Algolia Overwrites
/*************************/
.DocSearch-Button {
   padding: 8px !important;
   margin: 0 !important;
   color: #555 !important;
}

.DocSearch-Button-Placeholder, .DocSearch-Button-Keys {
   display: none !important;
   font-family: "SourceSansPro" !important;

   @media(min-width: 1370px) {
      display: flex !important;
   }
}
.DocSearch-Button-Placeholder {
   font-size: 18px !important;
}
.DocSearch-Button-Keys {
   font-size: 12px !important;
}

:root {
   --docsearch-color-primary: #1f498f !important;
   --docsearch-highlight-color:  #1f498f !important;
}

.DocSearch-Button:active, .DocSearch-Button:focus, .DocSearch-Button:hover, .DocSearch-Form {
   box-shadow: inset 0 0 0 2px #1f498f !important;
}

.DocSearch-Logo {
   display: none;
   svg {
      .cls-1, .cls-2 {
         fill: #999 !important;
      }
   }
} 

.DocSearch-Container {
   z-index: 1010 !important;
}

/*************************
   usa-banner
/*************************/

.usa-banner__inner {
   margin-left: 0;
}

.usa-banner__header--expanded .usa-banner__inner {
   margin-left: 0;
}

.usa-banner__button {
   font-family: SourceSansPro;
}


/*************************
   usa-nav
/*************************/
.usa-overlay.is-visible {
   z-index: 1005;
}
.usa-header--basic {
   .usa-nav {
      width: 80%;
      z-index: 1006;
      @media (min-width: 1024px) {
         padding: 0;
         width: 100%;
      }
   }
}

@keyframes slidein-left {
   0% {
      transform: translateX(100%);
   }
}

@media (min-width: 1024px) {

   .usa-nav__link,
   .usa-nav .usa-accordion__button {
      text-transform: uppercase;
      font-size: .85em;
   }

   .usa-nav__primary button[aria-expanded=true] {
      background-color: var(--hmda-blue);
   }

   .usa-nav__submenu .usa-nav__submenu-item {
      border-bottom: 1px solid rgba(255,255,255,.1);
      margin: 0;
      background-color: var(--hmda-blue);
      

      a {
         display: block;
         padding: .75em 1em .75em 2em;

         &:hover {
            background-color: var(--hmda-blue-light);
         }
      }
   }
}
.usa-nav__submenu-item .subMenuHeading {
   padding-left: 1em;

   @media (min-width: 1024px) {
      background-color: rgba(0,0,0,.25);
      padding-left: 1em;
      color: #fff;
      font-weight: bold;
   }
}


/*************************
   Logged In User
/*************************/
.user {
   display: flex;
   justify-content: center;
   align-items: center;
   position: absolute;
   top: 100px;
   right: 0;
   @media (min-width: 550px) {
      top: 2px;
   }
   p {
      margin: 0;
   }
   button {
      margin: 0 1em 0 0;
      font-size: .75em;
      padding: .1em 1em;
      background-color: transparent;
      color: #555;
      vertical-align: top;

      text-decoration-color: #1f498f;
      text-decoration-thickness: 3px;
      text-underline-offset: 5px;

      &:hover {
         background-color: transparent;
         text-decoration: underline;
         color: #555;
      }
   }
   @media (min-width: 1024px) {
      button {
         padding: .15em 1em;
      }
   }

}

/*************************
   usa-header
/*************************/

.usa-nav-container {
   max-width: 100%;

   @media screen and (min-width: 1024px) {
      border-bottom: 1px solid #dfe1e2;
      padding-right: 1em;
   }

}

.usa-header--basic {
   .usa-navbar {
      width: auto;
      height: 4.8rem;

      .usa-menu-btn {
         background-color: var(--hmda-blue);
         &:hover {
            background-color: var(--hmda-blue-light);
         }
      }

      .usa-logo {
         margin-top: 0;

         @media (min-width: 1024px) {
            margin-top: .75rem;
         }

         a {
            display: flex;
            align-items: center;
            color: var(--hmda-blue);

            &:hover {
               color: var(--usa-blue);
            }

            img {
               margin-right: .5em;
            }

            span {
               font-size: 1.2em;

               @media screen and (min-width: 1024px) {
                  display: none;
               }

               @media screen and (min-width: 1240px) {
                  display: inline-flex;
                  font-size: 1em;
                  min-width: 14em;
               }
            }
         }
      }
   }
}


/*************************
   usa-hero
/*************************/


.usa-hero {
   background-image: url("../../images/hmda-hero.min.jpg");
   background-position: 50% 0%;
   max-width: 1500px;
   margin: 0 auto;
   height: 450px;

   @media (min-width: 640px) {
      height: 500px;
      margin-top: 5px;
   }

   .grid-container {
      margin-left: 0;
      @media (min-width: 70em) {
         margin-left: 5%;
      }
      @media (min-width: 90em) {
         margin-left: 10%;
      }
   }
   

   .usa-hero__callout {
      background-color: var(--hmda-blue-dark-opacity);

      @media (min-width: 640px) {
         max-width: 41rem;
      }

      .usa-hero__heading {
         margin-top: 0rem;
         font-size: 3rem;
      }

      .hero__highlight {
         color: #fff;
         background-color: var(--hmda-blue-opacity);
         font-size: .7em;
         padding: .25em .25em;
         margin-bottom: .1em;
         display: inline-block;
      }

      .usa-hero__heading--alt {
         font-size: 3.25rem;
         margin-bottom: 1rem;
      }

      p {
         color: #fff;
      }

      @media (min-width: 30em) {
         .usa-button-group {
            flex-direction: column;
            .usa-button-group__item {
               width: 100%;
               margin: 0 0 .5em;
            }
         }  
      }

      .usa-button {
         color: #fff;
         background-color: var(--hmda-blue);
         text-shadow: 0 0 3px #000;
         border: 1px solid rgba(255,255,255,.25);

         svg {
            filter: drop-shadow(1px 1px 2px rgb(0 0 0 / 1));
         }

         &.primary {
            background-color: var(--hmda-blue-bright);
         }

         &:hover {
            background-color: var(--hmda-blue-light);
         }

         @media (min-width: 30em) {
            width: 100%;
         }
      }

      

      .usa-icon {
         font-size: 1.25em;
         vertical-align: text-bottom;
         margin-right: 0.25em;
      }
   }
}

/*************************
   Modal Video Container
/*************************/

.usa-modal-overlay {
   overflow: hidden;
}

.usa-modal--lg {
   position: relative;
   width: 78.5vw;
   height: 45.25vw;
   max-width: 1120px;
   max-height: 647px;
   padding: 1vw;

   .video-container {
      position: relative;
      width: 100%;
      height: 100%;
   }


   iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
   }


   .usa-modal__close {
      position: absolute;
      top: -1em;
      right: 0;
      z-index: 2;
      background-color: transparent !important;
      border: none !important;

      svg {
         fill: #fff;
 
         &:hover {
            fill: #0071bc;
         }
      }

   }
}


/*************************
   Quicklinks
/*************************/

#quicklinks {
   background-color: var(--hmda-blue);
   display: flex;
   flex-direction: column;
   text-align: center;
   font-size: 1.5em;
   position: relative;
   max-width: 1500px;
   padding: 0 0 .5em 0;

   @media (min-width: 40em) {
      flex-direction: row;
      padding: 0;
      margin: 0 auto;
   }
   @media (min-width: 85em) {
      padding: 0 10%;
   }

   .title {
      position: relative;
      padding: .25em 0;
      background-color: rgba(#000, .25);
      border: 1px solid rgba(#fff, .25);
      border-width: 1px 0;
      display: block;
      color: #fff;

      @media (min-width: 40em) {
         border-width: 0 1px;
         width: 35px;

         span {
            transform: rotate(-90deg);
            transform-origin: left bottom;
            position: absolute;
            left: 1.5em;
            bottom: 1em;
            font-size: .75em;
            display: block;
         }
      }

      @media (min-width: 56em) {
         span {
            bottom: .75em;
         }
      }
   }


   a, a:visited {
      //text-transform: uppercase;
      text-decoration: none;
      display: block;
      padding: .5em 1em;
      border: 1px solid rgba(#fff, .25);
      border-radius: .25em;
      color: #fff;
      text-align: left;
      margin: .25em 1em;
      flex: 1;

      &:hover {
         background-color: var(--hmda-blue-light);
      }

      svg {
         height: 2em;
         width: 2em;
         margin-right: .25em;
         vertical-align: bottom;
      }

      span {
         display: inline-block;
         font-size: .75em;
         text-align: left;
      }

      @media (min-width: 40em) {
         display: flex;
         flex-direction: column;
         align-items: center;
         margin: .25em;
         
         span {
            font-size: .6em;
            text-align: center;
         }
      }
      @media (min-width: 56em) {
         flex-direction: row;
         margin: .75em .5em;
         span {
            text-align: left;
         }
      }
   }
}

/*************************
   Homepage Overwrites
/*************************/

.home {
   h1 {
      color: var(--hmda-blue);
   }
}

/*************************
   tableOfContents w/usa-card
/*************************/

#tableOfContents {
   padding: 4rem 0 0;

   a, a:link, a:active, a:focus {
      color: var(--hmda-blue-light);
      line-height: 2em;
      .new-indicator {
         background-color: var(--hmda-blue-light);
      }
      
   }
   a:visited {
      color: var(--hmda-blue);
      .new-indicator {
         background-color: var(--hmda-blue);
      }
   }
   a:hover {
      color: var(--hmda-blue-bright);
      .new-indicator {
         background-color: var(--hmda-blue-bright);
      }
   }

   h2 {
      background-color: var(--hmda-blue);
      color: #fff;
      padding: 1em;
      margin-bottom: 1.5em;
      font-size: 1.17em;

      .usa-icon {
         vertical-align: bottom;
         height: 1.5em;
         width: 1.5em;
         margin-right: .25em;
      }
   }

   .usa-card-group {
      margin: 0;
      padding: 0;
      

      article {
         h3 {
            color: var(--hmda-blue);
            font-size: 1.25em;
         }
         margin-bottom: 2em;
         padding: 2em;
         border: 1px solid #ddd;  
         border-top: .75em solid var(--hmda-blue);
         width: 100%;
      }
   }

   #dataInfo {
      h2 {
         background-color: var(--hmda-blue-light);
      }
      article {
         h3 {
            color: var(--hmda-blue-light);
         }
         border-top: .75em solid var(--hmda-blue-light);
      }
   }
}


/*************************
   Mailing List Signup
/*************************/

.mainFooter .MailingListSignupForm {
   display: flex;
   flex-direction: column;
   padding: 1em 10% !important;
   margin: 1em auto 0;
   background-color: #eee !important;

   @media screen and (min-width: 1200px) {
      flex-direction: row;
      align-items: baseline;
      justify-content: center;

      &.small #email {
         width: 40%;
         margin-left: 1em !important;
         margin-right: 1em !important;
      }
   }
}


/*************************
   USWDS Footer
/*************************/

.usa-identifier {
   background-color: #1f498f;
   .usa-identifier__identity-domain {
      color: #fff;
   }
   .usa-identifier__required-link, .usa-identifier__required-link.usa-link, .usa-identifier__section--usagov a, .usa-identifier__identity-disclaimer a{ 
      &:link, &:visited, &:active {
         color: #fff;
         text-decoration: none;
      }
      &:hover {
         text-decoration: underline;
      } 
   }
}
